.intro-card {
  position: relative;
}

picture {
  display: none;
}

.teaser-text {
  width: 100%;

  span:nth-of-type(2) {
    white-space: nowrap;
  }
}

.teaser-text.right {
  text-align: right;
  margin-top: 1em;

  span {
    white-space: nowrap;
  }
}

.studio-image {
  width: 100%;
  border-radius: 50% 50% 0px 0px;
  overflow: hidden;
}

.studio-card {
  width: 100%;
}

.map-image {
  width: 100%;
}

@media screen and (min-width: $mobile-breakpoint) and (max-resolution: $mobile-resolution) {
  .studio-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .intro-card {
    flex-basis: 50%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
  }
  .teaser-text.right:before {
    content: "";
    width: 8em;
    height: 8em;
    border-radius: 4em;
    position: absolute;
    border: 1px solid $color-navbar;
    left: 20em;
    top: 3em;
    z-index: -10;
  }
  .map-info {
    flex-basis: 50%;
    flex-grow: 1;
  }
  .studio-image {
    width: 400px;
    border-radius: 200px 200px 0px 0px;
    overflow: hidden;
  }
  picture {
    display: block;
    position: relative;
  }
  .studio-image:before {
    content: "";
    border: 1px solid $color-navbar;
    z-index: -10;
    position: absolute;
    top: 2em;
    left: 2em;
    height: 100%;
    width: 410px;
    border-radius: inherit;
  }
  .joga-image {
    width: 500px;
    height: 650px;
    overflow: hidden;

    picture {
      top: -100px;
    }
  }
  .joga-image:before {
    content: "";
    border: 1px solid $color-navbar;
    width: 500px;
    height: 100%;
    z-index: -10;
    position: absolute;
    top: 2em;
    left: 2em;
  }
  .map-image {
    width: auto;
  }
}
